@import url(https://fonts.googleapis.com/css2?family=Special+Elite&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.rules-container {
  max-width: 80vw;
  margin: 2rem auto;
  border: 2px dashed var(--grey);
  padding: 2rem;
  border-radius: 12px;
  line-height: 2;
}

.rules-container .btn {
  display: inline-block;
}

.rules-container .btn.right {
  float: right;
  font-size: 1.5rem;
  padding: 0 1rem;
}

.card {
  border: 1px solid;
  cursor: pointer;
  margin: 1rem;
  position: relative;
  border-radius: 12px;
}

.card .context-menu {
  position: absolute;
  z-index: 2;
  left: 20%;
  top: 20%;
  width: 100%;
}

.badge.card-id {
  left: 0;
  position: absolute;
  top: 0;
  height: 15%;
  width: 15%;
  border-radius: 8px;
  padding: 4px 3px 0 3px;
}

.card-img {
  width: 100%;
  border-radius: 12px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.card-img.enlarged {
  border: 2px solid;
  left: -50%;
  position: absolute;
  top: -50%;
  width: 200%;
  z-index: 2;
}

.overlay {
  border: 4px solid;
  box-sizing: border-box;
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  border-radius: 12px;
}

.overlay.red {
  background-color: var(--red-extra-light);
  border-color: var(--red);
}

.overlay.blue {
  background-color: var(--blue-extra-light);
  border-color: var(--blue);
}

.overlay.neutral {
  background-color: var(--yellow-extra-light);
  border-color: var(--yellow);
}

.overlay.black {
  background-color: rgba(0, 0, 0, 0.7);
  border-color: rgb(0, 0, 0);
}

.context-menu {
    background-color: white;
    border: 2px solid #000;
    border-radius: 5px;
}

.context-menu .menu-action {
    border-bottom: 1px dashed;
    margin: 0;
    padding: .7rem 1rem;
}

.context-menu .menu-action:last-of-type {
    border-bottom: none;
}

.context-menu .menu-action:not(.neutral):hover {
    color: white;
    cursor: pointer;
    border-color: black;
}

.context-menu .menu-action.red:hover {
    background-color: var(--red-light);
}

.context-menu .menu-action.blue:hover {
    background-color: var(--blue-light);
}

.context-menu .menu-action.neutral:hover {
    background-color: var(--yellow-light);
}

.context-menu .menu-action.black:hover {
    background-color: rgba(0, 0, 0, 0.7);
}

.modal {
  width: 100vw;
  height: 100vh;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1040;
  background: rgba(88, 88, 88, 0.7);;
}

.modal-content {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  background: var(--background);
  border-radius: 12px;
  border: 3px solid;
  position: relative;
  padding: 3rem;
  box-sizing: border-box;
  max-width: calc(100vw - 2rem);
  max-height: calc(100vh - 2rem);
}

.modal-content .button-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.close-modal {
  position: absolute;
  right: 1rem;
  top: 1rem;
  font-size: 2rem;
  background: none;
  border: none;
  font-weight: bold;
  cursor: pointer;
  font-family: inherit;
}

.modal-title {
  font-size: 2.5rem;
  text-align: center;
  margin-top: 0;
}

.teams-wrapper {
  display: flex;
  flex: 1 1 auto;
}

.teams-wrapper .team {
    flex: 1 1;
    padding: 0 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.teams-wrapper .input {
    display: block;
    margin-bottom: 1rem;
}

.teams-wrapper .team-title {
    margin-top: 0;
    display: flex;
}

.teams-wrapper .team-blue .team-title {
    color: var(--blue-light)
}

.teams-wrapper .team-red .team-title {
    color: var(--red-light)
}

.teams-wrapper .team .team-title .starting-team {
    display: inline-block;
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    margin-left: .5rem;
    cursor: pointer;
    opacity: 0.7;
}

.teams-wrapper .team .team-title .starting-team.active {
    opacity: 1;
}

.teams-wrapper .team-red .team-title .starting-team {
    background-color: var(--red);
}

.teams-wrapper .team-blue .team-title .starting-team {
    background-color: var(--blue);
}

.teams-wrapper .team-red .team-title .starting-team.active {
    box-shadow: 0px 0px 7px 0px var(--red);
}

.teams-wrapper .team-blue .team-title .starting-team.active {
    box-shadow: 0px 0px 7px 0px var(--blue);
}

.player-wrap {
    position: relative;
}

.teams-wrapper form {
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
    align-items: center;
}

.teams-wrapper .players {
    flex: 1 1 auto;
    max-height: 200px;
    margin-bottom: 1rem;
    border-bottom: 2px dashed;
    overflow: scroll;
}

.remove-player {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 1rem;
    background: none;
    border: none;
    font-family: inherit;
    cursor: pointer;
}

.score-board {
    position: absolute;
    background: var(--background);
    z-index: 5;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    border: 2px solid;
    border-top: none;
    transition: all .2s;
    min-width: 400px;

    top: 0;
    left: 50%;
    transform: translate(-50%, -100%);
}

.score-board.expanded {
    top: 0;
    left: 50%;
    transform: translateX(-50%);
}

.score-board:after {
    content: "^";
    position: absolute;
    left: 50%;
    transform: translate(-50%, -60%) rotate(180deg);
    width: 20px;
    display: flex;
    height: 20px;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
    transition: all .2s;
    cursor: pointer;
}

.score-board.expanded:after {
    opacity: 0;
}

.score-board .score-wrapper {
    display: flex;
    justify-content: center;
    padding: 0 2rem 2rem;
}

.score-board .score-wrapper .team-score:last-of-type {
    margin-left: 5rem;
}

.team-score {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.team-score .score {
    text-align: center;
    margin-bottom: 0;
}

.team-score.team-red .score {
    color: var(--red-light);
}

.team-score.team-blue .score {
    color: var(--blue-light);
}


.team-summary.team-blue .team-title,
.team-summary.team-blue .spymaster {
    color: var(--blue-light)
}

.team-summary.team-red .team-title,
.team-summary.team-red .spymaster {
    color: var(--red-light)
}

.team-summary .team-title {
    margin-top: 0;
}

.team-summary .names {
    list-style: none;
    text-align: left;
    padding: 0;
}

.team-summary + .team-summary {
    margin-top: 2rem;
}

.menu {
  border-radius: 6px;
  border: 2px solid var(--green-light);
  min-width: 120px;
}

.menu-content {
  display: none;
}

.menu.expanded .menu-content {
  display: block;
}

.menu-item > *,
.menu-trigger {
  display: block;
  color: #000;
  color: initial;
  font-size: 1rem;
  font-weight: 500;
  padding: 0.5rem 1rem;
  cursor: pointer;
  transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}

.menu-trigger {
  text-align: center;
}

.menu-item {
  display: block;
  border-bottom: 2px solid var(--green);
  color: #000;
  color: initial;
}

.menu-item > *:hover,
.menu-trigger > *:hover {
  background-color: var(--green-light);
  border-color: var(--green);
  color: white;
}

.menu-trigger::after {
  content: "⋀";
  margin-left: .3rem;
}

.menu.expanded .menu-trigger::after {
  content: "⋁";
}

.container {
  align-items: center;
  display: flex;
  min-height: 100vh;
  justify-content: center;
  width: 100%;
}

.inner-container {
  align-items: center;
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 1rem;
}

.grid {
  display: grid;
  grid-template-columns: 250px 250px 250px 250px 250px;
  grid-template-rows: 250px 250px 250px 250px;
}

.side-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-self: stretch;
  margin: 1rem 0;
}

.side-wrapper .teams-summary {
  flex: 1 1 auto;
}

.badge {
  align-items: center;
  background: var(--background);
  border-radius: 50%;
  display: inline-flex;
  font-size: 1rem;
  justify-content: center;
  margin: 0.3rem;
  color: #000;
  width: 1.5rem;
  height: 1.5rem;
}

.badge.has-total {
  width: auto;
  border-radius: 6px;
  height: auto;
  padding: .2rem;
}

.btn {
  border-radius: 6px;
  border: 2px solid #000;
  color: #000;
  color: initial;
  cursor: pointer;
  display: block;
  font-size: 1rem;
  font-weight: 500;
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  text-decoration: none;
  transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
  font-family: inherit;
}

.btn:hover {
  background-color: var(--green-light);
  border-color: var(--green);
  color: white;
}

.btn.blue {
  border-color: var(--blue-light);
}

.btn.blue:hover {
  background-color: var(--blue-light);
  border-color: var(--blue);
}

.btn.green {
  border-color: var(--green-light);
}

.btn.green:hover {
  background-color: var(--green-light);
  border-color: var(--green);
}

@media only screen and (max-height: 1000px) {
  .grid {
    display: grid;
    grid-template-columns: 220px 220px 220px 220px 220px;
    grid-template-rows: 220px 220px 220px 220px;
  }
}

@media only screen and (max-height: 800px) {
  .grid {
    display: grid;
    grid-template-columns: 25vh 25vh 25vh 25vh 25vh;
    grid-template-rows: 25vh 25vh 25vh 25vh;
  }
}

@media only screen and (max-width: 1300px) {
  .grid {
    display: grid;
    grid-template-columns: 16vw 16vw 16vw 16vw 16vw;
    grid-template-rows: 16vw 16vw 16vw 16vw;
  }
}

a {
  text-decoration: none;
  color: var(--blue);
}

.find-card-wrapper {
  text-align: center;
}

.input {
  padding: 0.5rem 1rem;
  border: 2px dashed var(--grey);
  border-radius: 4px;
  font-size: 1rem;
  font-family: inherit;
}

.find-card-wrapper .btn {
  display: inline-block;
  margin-left: 1rem;
}

.container.spy-master {
  flex-direction: column;
  min-height: 80vh;
}

.card-container {
  display: flex;
  align-items: center;
}

.title {
  font-size: 3.5rem;
}

.spy-grid {
  background-color: black;
  border-radius: 12px;
  display: grid;
  grid-template-columns: 150px 150px 150px 150px 150px;
  grid-template-rows: 125px 125px 125px 125px;
  padding: 15px;
}

.spy-grid .card {
  border-radius: 12px;
  margin: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.spy-grid .card.red {
  background: rgb(255,77,94);
  background: radial-gradient(circle, rgba(255,77,94,1) 0%, rgba(201,34,50,1) 100%);
}

.spy-grid .card.blue {
  background: rgb(72,168,255);
  background: radial-gradient(circle, rgba(72,168,255,1) 0%, rgba(72,122,167,1) 100%);
}

.spy-grid .card.neutral {
  background-color: #daca9e;
}

.spy-grid .card.black {
  background: rgb(99,99,99);
  background: radial-gradient(circle, rgba(99,99,99,1) 0%, rgba(62,60,58,1) 100%);
}

.spy-grid .card::before {
  font-weight: 900;
  font-size: 3rem;
}

.spy-grid .card.black::before {
  content: '✕';
  text-shadow: 0 0 5px #FFF, 0 0 10px #FFF, 0 0 15px #FFF, 0 0 20px #b9b9b9, 0 0 30px #b9b9b9, 0 0 40px #b9b9b9, 0 0 55px #b9b9b9, 0 0 75px #b9b9b9;
}

.spy-grid .card.red::before {
  content: '◇';
  color: #f73434;
  font-size: 5rem;
  text-shadow: 0 0 5px #FFF, 0 0 10px #FFF, 0 0 15px #FFF, 0 0 20px #ff1818, 0 0 30px #ff1818, 0 0 40px #ff1818, 0 0 55px #ff1818, 0 0 75px #ff1818;
}

.spy-grid .card.blue::before {
  content: '○';
  color: #0088ff;
  font-size: 5rem;
  text-shadow: 0 0 5px #FFF, 0 0 10px #FFF, 0 0 15px #FFF, 0 0 20px #0088ff, 0 0 30px #0088ff, 0 0 40px #0088ff, 0 0 55px #0088ff, 0 0 75px #0088ff;
}

.starting-color {
  border-radius: 8px;
  height: 100px;
  margin: 15px;
  width: 30px;
}

.starting-color.red {
  background: rgb(255,77,94);
  background: radial-gradient(circle, rgba(255,77,94,1) 0%, rgba(201,34,50,1) 100%);
  box-shadow: 0px 0px 21px 2px rgba(255,77,94,1);
  -moz-box-shadow: 0px 0px 21px 2px rgba(255,77,94,1);
  -webkit-box-shadow: 0px 0px 21px 2px rgba(255,77,94,1);
}

.starting-color.blue {
  background: rgb(72,168,255);
  background: radial-gradient(circle, rgba(72,168,255,1) 0%, rgba(72,122,167,1) 100%);
  box-shadow: 0px 0px 21px 2px rgba(72,168,255,1);
  -moz-box-shadow: 0px 0px 21px 2px rgba(72,168,255,1);
  -webkit-box-shadow: 0px 0px 21px 2px rgba(72,168,255,1);
}


@media only screen and (max-width: 900px) {
  .title {
    font-size: 2.5rem;
  }

  .spy-grid .card::before {
    font-size: 2rem;
  }

  .spy-grid .card.red::before, .spy-grid .card.blue::before {
    font-size: 2.5rem;
  }

  .spy-grid {
    grid-template-columns: 100px 100px 100px 100px 100px;
    grid-template-rows: 75px 75px 75px 75px;
  }
}


@media only screen and (max-width: 650px) {
  .spy-grid {
    grid-template-columns: 75px 75px 75px 75px 75px;
    grid-template-rows: 55px 55px 55px 55px;
  }
  .spy-grid .card::before {
    font-size: 0.7rem;
  }
  .spy-grid .card.blue::before, .spy-grid .card.red::before {
    font-size: 1rem;
  }
  .starting-color {
    height: 60px;
    margin: 10px;
    width: 20px;
  }
}


@media only screen and (max-width: 525px) {
  .title {
    font-size: 2rem;
  }
  .spy-grid {
    grid-template-columns: 50px 50px 50px 50px 50px;
    grid-template-rows: 40px 40px 40px 40px;
  }
}

body {
  font-family: 'Special Elite', cursive;
}

:root {
  --background:  #f9e5a9;
  --grey: gainsboro;
  --blue: rgb(0, 149, 255);
  --blue-light: rgba(0, 149, 255, 0.8);
  --blue-extra-light: rgba(0, 149, 255, 0.4);
  --yellow: rgb(255, 212, 0);
  --yellow-light: rgba(255, 212, 0, 0.8);
  --yellow-extra-light: rgba(255, 212, 0, 0.4);
  --red: rgb(255, 0, 0);
  --red-light: rgba(255, 0, 0, 0.8);
  --red-extra-light: rgba(255, 0, 0, 0.4);
  --green: rgb(0, 177, 6);
  --green-light: rgba(0, 177, 6, 0.8);
  --green-extra-light: rgba(0, 177, 6, 0.4);
}

